import React, { useEffect, useMemo, useState } from 'react';
import BootcampSelect from './BootcampSelect';
import useGet from '../Hooks/Get';
import {PROMO_TYPES,DISCOUNT_TYPES} from '../../PublicComponents/Components/Enum'

const PromoCodeForm = ( {action} ) => {
  
  const [bootcamps, setBootcamps] = useState([]);
  const [emails, setEmail] = useState(null);
  const [promoType, setPromoType] = useState(PROMO_TYPES.PARTICULAR_BOOTCAMP);
  const [discountType, setDiscountType] = useState(DISCOUNT_TYPES.FLAT);
  const [discountValue, setDiscountValue] = useState('');
  const [maxDiscount, setMaxDiscount] = useState(0);
  const [bootcampId, setBootcampId] = useState(null);
  const [expirationDate, setExpirationDate] = useState('');
  const [isActive, setIsActive] = useState(true);
  const [errors, setErrors] = useState({});
  const [minDateTime, setMinDateTime] = useState('');

  const bootcampUrl = useMemo(() => {
    if (bootcamps.length < 1) return null;
    const bootcampIds = bootcamps.map(bootcamp => bootcamp.value).join(',');
    setBootcampId(bootcampIds);
    
    return `/registrations/getemails?bootcampIds=${bootcampIds}`;
  }, [bootcamps]);

  const { data } = useGet(bootcampUrl);

  useEffect(() => {
    if (data && data.length > 0 && promoType != PROMO_TYPES.PARTICULAR_USER_AND_BOOTCAMP) {
      const emailList = data.map((item) => item.email).join(',');
      setEmail(emailList);
    }
  }, [data]);

  useEffect(() => {
    const now = new Date();
    const minDateTimeString = now.toISOString().slice(0, 10);
    setMinDateTime(minDateTimeString);
  }, []);
 

  const handleSubmit = (e) => {
    e.preventDefault();
    const validationErrors = {};

    // Validation logic
    if ((promoType === PROMO_TYPES.PARTICULAR_BOOTCAMP || promoType == PROMO_TYPES.PARTICULAR_USER_AND_BOOTCAMP) && !bootcampId) {
      validationErrors.bootcampId = "Bootcamp selection is required";
    }

    if ((promoType === PROMO_TYPES.PARTICULAR_USERS || promoType == PROMO_TYPES.PARTICULAR_USER_AND_BOOTCAMP) && !emails) {
      validationErrors.emails = "Emails are required for this promo type";
    }
    if (!discountValue || discountValue <= 0) {
      validationErrors.discountValue = "Enter a valid discount value";
    }
    if (discountType === DISCOUNT_TYPES.PERCENTAGE && ((!discountValue || discountValue <= 0||discountValue>=100))) {
      validationErrors.discountValue = "Enter a valid maximum discount value";
    }
    if(discountType == DISCOUNT_TYPES.UPTO_PERCENTAGE && maxDiscount<=0){
      validationErrors.maxDiscount = "enter the valid max discount"
    }
    if (discountType !== DISCOUNT_TYPES.UPTO_PERCENTAGE) {
      setMaxDiscount(0);
    }
    setErrors(validationErrors);

    if (Object.keys(validationErrors).length === 0) {
      const promoData = {
        promo_type: promoType,
        discount_type: discountType,
        discount_value: parseFloat(discountValue),
        max_discount: maxDiscount ? parseFloat(maxDiscount) : null,
        bootcamp_id: bootcampId || null,
        emails: emails || null,
        expiration_date: expirationDate || null,
        is_active: isActive,
      };

      console.log(promoData);
      action(promoData);
      e.target.reset();
    }
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        {/* Promo Type */}
        <div className="flex flex-col mb-4">
          <label htmlFor="promo-type" className="text-lg font-semibold mb-2">Promo Type:</label>
          <select
            id="promo-type"
            name="promo-type"
            value={promoType}
            onChange={(e) => setPromoType(e.target.value)}
            className="p-2 border border-black rounded"
          >
            {Object.entries(PROMO_TYPES).map(([key, value]) => (
              <option key={key} value={value}>{key.replace('_', ' ')}</option>
            ))}
          </select>
        </div>
        <div className="flex flex-col mb-4">
          <label htmlFor="discount-type" className="text-lg font-semibold mb-2">Discount Type:</label>
          <select
            id="discount-type"
            name="discount-type"
            value={discountType}
            onChange={(e) => setDiscountType(e.target.value)}
            className="p-2 border border-black rounded"
          >
            {Object.entries(DISCOUNT_TYPES).map(([key, value]) => (
              <option key={key} value={value}>{key.replace('_', ' ')}</option>
            ))}
          </select>
        </div>

        {/* Discount Value */}
        <div className="flex flex-col mb-4">
          <label htmlFor="discount-value" className="text-lg font-semibold mb-2">Discount {discountType === DISCOUNT_TYPES.FLAT ? 'Amount' : 'Percentage'}:</label>
          <input
            type="number"
            id="discount-value"
            name="discount-value"
            value={discountValue}
            onChange={(e) => setDiscountValue(e.target.value)}
            className={`p-2 border rounded ${errors.discountValue ? 'border-red-500' : 'border-black'}`}
            required
            min="1"
          />
          {errors.discountValue && <span className="text-red-500 text-sm">{errors.discountValue}</span>}
        </div>

        {/* Max Discount */}
        <div className="flex flex-col mb-4">
          <label htmlFor="max-discount" className="text-lg font-semibold mb-2">Max Discount:</label>
          <input
            type="number"
            id="max-discount"
            name="max-discount"
            value={maxDiscount}
            onChange={(e) => setMaxDiscount(e.target.value)}
            className="p-2 border border-black rounded"
            disabled={discountType !== DISCOUNT_TYPES.UPTO_PERCENTAGE}
            readOnly={discountType !== DISCOUNT_TYPES.UPTO_PERCENTAGE}
            placeholder={discountType !== DISCOUNT_TYPES.UPTO_PERCENTAGE ? 'N/A' : ''}
          />
          {errors.maxDiscount && <span className="text-red-500 text-sm">{errors.maxDiscount}</span>}
        </div>

        {/* Conditionally render bootcamp and email fields based on promo type */}
        {promoType && promoType !== PROMO_TYPES.ANYONE &&
          <div className="flex flex-col mb-4">
            <label htmlFor="bootcamp" className="text-lg font-semibold mb-2">{promoType === PROMO_TYPES.PARTICULAR_USERS ? 'Export from Bootcamps' : 'Bootcamps'}:</label>
            <BootcampSelect selectedBootcamp={bootcamps} setSelectedBootcamp={setBootcamps} isMulti={true} isBootcampActive ={PROMO_TYPES.PARTICULAR_USERS!=promoType}/>
            {errors.bootcampId && <span className="text-red-500 text-sm">{errors.bootcampId}</span>}
          </div>
        }

        {(promoType === PROMO_TYPES.PARTICULAR_USERS || promoType == PROMO_TYPES.PARTICULAR_USER_AND_BOOTCAMP )&&
          <div className="flex flex-col mb-4">
            <label htmlFor="emails" className="text-lg font-semibold mb-2">Enter Emails:</label>
            <input
              type="text"
              id="emails"
              name="emails"
              value={emails}
              onChange={(e) => setEmail(e.target.value)}
              className={`p-2 border rounded ${errors.emails ? 'border-red-500' : 'border-black'}`}
            />
            {errors.emails && <span className="text-red-500 text-sm">{errors.emails}</span>}
          </div>
        }

        {/* Other form fields */}
        <div className="flex flex-col mb-4">
          <label htmlFor="expiration-date" className="text-lg font-semibold mb-2">Expiration Date:</label>
          <input
            type="date"
            id="expiration-date"
            name="expiration-date"
            value={expirationDate}
            min={minDateTime}
            onChange={(e) => setExpirationDate(e.target.value)}
            className="p-2 border rounded"
          />
        </div>

        <div className="flex items-center mb-4">
          <input
            type="checkbox"
            id="is-active"
            name="is-active"
            checked={isActive}
            onChange={(e) => setIsActive(e.target.checked)}
            className="mr-2"
          />
          <label htmlFor="is-active" className="text-lg font-semibold">Is Active</label>
        </div>

        <button type="submit" className="bg-blue-500 text-white py-2 px-4 rounded">Add Promo Code</button>
      </form>
    </div>
  );
};

export default PromoCodeForm;
